<template>
  <div class="p2">
    <h1 class="uk-text-center uk-margin-large-top uk-margin-large-bottom">广告费和业务宣传费跨年度纳税调整明细表</h1>
    <form>
      <table style="margin:0px auto;" cellpadding="0" cellspacing="0" border="1" width="100%">
        <tbody>
        <tr>
          <td class="uk-text-center">行次</td>
          <td class="uk-text-center">项目</td>
          <td class="uk-text-center">金额</td>
        </tr>
        <tr>
          <td class="uk-text-center">1</td>
          <td class="uk-text-left">一、本年广告费和业务宣传费支出</td>
          <td class="uk-text-right bg-write"><input type="text" name="1_1" value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">2</td>
          <td class="uk-text-left">　　减：不允许扣除的广告费和业务宣传费支出</td>
          <td class="uk-text-right bg-write"><input type="text" name="2_1" value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">3</td>
          <td class="uk-text-left">二、本年符合条件的广告费和业务宣传费支出（1-2）</td>
          <td class="uk-text-left bg-unwrite"><input type="text" name="3_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">4</td>
          <td class="uk-text-left">三、本年计算广告费和业务宣传费扣除限额的销售（营业）收入</td>
          <td class="uk-text-right bg-unwrite"><input type="text" name="4_1" v-model="form.a4_1" readonly></td>
        </tr>
        <tr>
          <td class="uk-text-center">5</td>
          <td class="uk-text-left">　　乘：税收规定扣除率</td>
          <td style="text-align: right"><input type="text" name="5_1" readonly value="0.15"></td>
        </tr>
        <tr>
          <td class="uk-text-center">6</td>
          <td class="uk-text-left">四、本企业计算的广告费和业务宣传费扣除限额（4×5）</td>
          <td class="uk-text-left bg-unwrite"><input type="text" name="6_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">7</td>
          <td class="uk-text-left">五、本年结转以后年度扣除额（3&gt;6,本行=3-6；3≤6，本行=0）</td>
          <td class="uk-text-left bg-unwrite"><input type="text" name="7_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">8</td>
          <td class="uk-text-left">　　加：以前年度累计结转扣除额</td>
          <td class="uk-text-right bg-write"><input type="text" name="8_1" value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">9</td>
          <td class="uk-text-left">　　减：本年扣除的以前年度结转额[3&gt;6，本行=0；3≤6，本行=8或（6-3）孰小值]</td>
          <td class="uk-text-left bg-unwrite"><input type="text" name="9_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">10</td>
          <td class="uk-text-left">六、按照分摊协议归集至其他关联方的广告费和业务宣传费（10≤3或6孰小值）</td>
          <td class="uk-text-right bg-write"><input type="text" name="10_1" value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">11</td>
          <td class="uk-text-left">　　按照分摊协议从其他关联方归集至本企业的广告费和业务宣传费</td>
          <td class="uk-text-right bg-write"><input type="text" name="11_1" value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">12</td>
          <td class="uk-text-left">七、本年广告费和业务宣传费支出纳税调整金额（3&gt;6，本行=2+3-6+10-11；3≤6，本行=2+10-11-9）</td>
          <td class="uk-text-left bg-unwrite"><input type="text" name="12_1" readonly value="0.00"></td>
        </tr>
        <tr>
          <td class="uk-text-center">13</td>
          <td class="uk-text-left">八、累计结转以后年度扣除额（7+8-9）</td>
          <td class="uk-text-left bg-unwrite"><input type="text" name="13_1" readonly value="0.00"></td>
        </tr>
        </tbody>
      </table>
    </form>
    <div style="margin-top: 15px" class="uk-text-center">
      <button @click="saveForm" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
      <button class="uk-button uk-button-primary" @click="dataRestore()"><i class="uk-icon-close"></i> 删除</button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-file-excel-o"></i> 导出Excel
      </button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-print"></i> 打印</button>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 17,  //当前表格的名称编号
        setting: [],
        form: {
          'a4_1': 0
        }
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
      //表间取值
      this.formrevise()
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        // this.setting = {'1_1': 10, '2_1': 5}
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        //第3行计算
        this.fn1('3_1', [1, 2], 1)
        //第6行计算
        this.fn1('6_1', [5, 4], 2)
        //第7行计算
        this.fn2(1)
        //第9行计算
        this.fn2(2)
        //第12行计算
        this.fn2(3)
        //第13行计算
        this.fn3()
      },
      // q 要计算单元格的name
      // jump 参与运算的行
      // type {
      // 1:a=b+c
      // 2:a=b*c
      // }
      fn1(q, jump, type) {

        if (J('input[name=' + jump[0] + '_1]').val()) {
          var sum = parseFloat(J('input[name=' + jump[0] + '_1]').val())
        } else {
          var sum = 0.00
        }
        for (let i = 1; i < jump.length; i++) {
          let dom = J('input[name=' + jump[i] + '_1]')
          if (dom.val()) {
            if (type == 1) {
              sum -= parseFloat(dom.val())
            } else if (type == 2) {
              sum = parseFloat(sum * this.form['a4_1'])
            }
          }
        }
        J('input[name=' + q + ']').val(sum.toFixed(2))
      },
      // type 区别行运算
      // 1:第7行的比较+运算
      // 2:第9行的比较+运算
      // 3:第12行的比较+运算
      fn2(type) {
        let sum = 0
        if (J('input[name=3_1]').val()) {
          var a3_1 = parseFloat(J('input[name=3_1]').val())
        } else {
          var a3_1 = 0
        }
        if (J('input[name=6_1]').val()) {
          var a6_1 = parseFloat(J('input[name=6_1]').val())
        } else {
          var a6_1 = 0
        }
        if (type == 1) {
          if (a3_1 > a6_1) {
            sum = a3_1 - a6_1
          } else {
            sum = 0
          }
          J('input[name=7_1]').val(sum.toFixed(2))
        } else if (type == 2) {
          if (a3_1 > a6_1) {
            sum = 0
          } else {
            if (J('input[name=8_1]').val()) {
              var a8_1 = parseFloat(J('input[name=8_1]').val())
            } else {
              var a8_1 = 0
            }
            let arr = a6_1 - a3_1
            if (a8_1 > arr) {
              sum = arr
            } else {
              sum = a8_1
            }
          }
          J('input[name=9_1]').val(sum.toFixed(2))
        } else if (type == 3) {
          if (a3_1 > a6_1) {
            // arr 参与运算的行
            let arr = [2, 3, 6, 10, 11]
            let sum = 0
            for (let i = 0; i < arr.length; i++) {
              if (J('input[name=' + arr[i] + '_1]').val()) {
                if (i != 2 && i != 4) {
                  sum += parseFloat(J('input[name=' + arr[i] + '_1]').val())
                } else {
                  sum -= parseFloat(J('input[name=' + arr[i] + '_1]').val())
                }
              }
            }
            $('input[name=12_1]').val(sum.toFixed(2))
          } else {
            // arr 参与运算的行
            let arr = [2, 10, 11, 9]
            let sum = 0
            for (let i = 0; i < arr.length; i++) {
              if (J('input[name=' + arr[i] + '_1]').val()) {
                if (i != 2 && i != 3) {
                  sum += parseFloat(J('input[name=' + arr[i] + '_1]').val())
                } else {
                  sum -= parseFloat(J('input[name=' + arr[i] + '_1]').val())
                }
              }
            }
            J('input[name=12_1]').val(sum.toFixed(2))
          }
        }
      },
      fn3() {
        // arr 参与运算的行
        let arr = [7, 8, 9]
        let sum = 0
        for (let i = 0; i < arr.length; i++) {
          if (J('input[name=' + arr[i] + '_1]').val()) {
            if (i != 2) {
              sum += parseFloat(J('input[name=' + arr[i] + '_1]').val())
            } else {
              sum -= parseFloat(J('input[name=' + arr[i] + '_1]').val())
            }
          }
        }
        J('input[name=13_1]').val(sum.toFixed(2))
      },
      formrevise() {
        var name1 = ''
        if (this.$store.state.tableData[12]) {
          this.form.a4_1 = this.$store.state.tableData[12][2].value
        } else {
          name1 = '《一般企业收入明细表》'
        }
        if (name1 !== '') {
          this.eg('请按照填表顺序正确填写:' + name1 + '表未填写！')
        }
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    max-width: 900px;
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input, select {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    padding: 3px 2px;
    text-align: left;
  }

  .bg-write {
    background: #d6ebff;
  }

  .bg-unwrite {
    background: #ccccff;
  }

  .bg-gray {
    background: #ddd;
  }
</style>



